<template>
  <aside class="aside-menu" :class="{ toggled: show }">
    <div class="aside-menu__fixed">
      <button type="button" class="menu-toggler" @click="handleAsideToggled">
        <span></span>
      </button>
      <div class="icons-bar">
        <div class="icons-bar__image active">
          <!-- /*@TODO add dynamic data*/ -->
          <avatar size="md" :file="require('@/assets/img/png/Image_8.png')" />
          <badge :info="12" />
        </div>
        <div class="icons-bar__image">
          <avatar size="md" :file="require('@/assets/img/png/Image_28.png')" />
        </div>
        <div class="icons-bar__image">
          <avatar size="md" :file="require('@/assets/img/png/Image_29.png')" />
          <!-- /*@TODO add dynamic data*/ -->
          <badge :info="154" />
        </div>
        <button class="icons-bar__image icons-bar__image--new-account">
          <plus-icon />
        </button>
      </div>
    </div>
    <div class="aside-menu__toggler">
      <nav class="aside-menu-navigation">
        <h3>Joker Movies</h3>
        <ul>
          <li class="aside-menu-navigation__link">
            <a href="#"
              >My blog
              <!-- /*@TODO add dynamic data*/ -->
              <badge :info="34" />
            </a>
          </li>
          <li class="aside-menu-navigation__link">
            <router-link :to="{ name: 'MyCommunities' }"
              >My Communities</router-link
            >
          </li>
          <li class="aside-menu-navigation__link">
            <router-link :to="{ name: 'Following' }">Following</router-link>
          </li>
          <li class="aside-menu-navigation__link">
            <router-link :to="{ name: 'Followers' }"
              >Followers
              <!-- /*@TODO add dynamic data*/ -->
              <badge :info="1555"
            /></router-link>
          </li>
          <li class="aside-menu-navigation__link">
            <a href="#">Activities</a>
          </li>
        </ul>
      </nav>
      <div class="aside-menu__btns">
        <div class="aside-menu__theme-toggler">
          Dark mode<form-toggler
            name="theme"
            class="form-toggler"
            @change="toggleTheme"
            :checked="themeDark"
          />
        </div>
        <button type="button" class="aside-menu__logout-btn" @click="logout">
          Logout
          <logout-icon class="logout-icon" />
        </button>
      </div>
    </div>
  </aside>
</template>
<script>
import { mapState, mapActions } from 'vuex';
import Badge from '~common/Badge';
import Avatar from '~common/Avatar';
import PlusIcon from '~svg/plus.svg';
import LogoutIcon from '~svg/logout.svg';
import FormToggler from '~common/Fields/FormToggler';

export default {
  name: 'fanexus-aside-menu',
  components: {
    PlusIcon,
    FormToggler,
    LogoutIcon,
    Avatar,
    Badge
  },
  props: {
    show: {
      type: Boolean,
      default: false,
      required: true
    }
  },
  methods: {
    /*@TODO add a corresponding side effect to the login action (if needed) */
    ...mapActions(['logout', 'toggleTheme']),
    handleAsideToggled() {
      this.$emit('asideToggled');
    }
  },
  computed: {
    ...mapState({
      themeDark: state => state.theme.themeDark
    })
  }
};
</script>
<style lang="scss" scoped>
.wrapper.logged .aside-menu {
  display: flex;
}
.aside-menu {
  grid-area: aside;
  display: none;
  overflow: hidden;
  width: 60px;
  transition: var(--transition-default);

  &.toggled {
    width: 260px;
    .aside-menu__toggler {
      left: 60px;
    }
  }

  &.toggled .menu-toggler:before {
    transform: rotate(45deg);
    left: 0px;
    top: 6px;
  }

  &.toggled .menu-toggler:after {
    opacity: 0;
  }

  &.toggled .menu-toggler span {
    transform: rotate(-45deg);
    left: 0px;
    top: 6px;
  }

  &__fixed {
    position: relative;
    z-index: var(--z-index-1);
    width: 60px;
    background: var(--BG-navbar);

    .menu-toggler {
      display: block;
      width: 19px;
      height: 12px;
      position: relative;
      margin: var(--element-margin-l) auto;
      background: none;
      transition: var(--transition-default);
      &:focus {
        outline: none;
      }
      &:focus {
        span {
          background-color: var(--BG-button-primary-hover);
        }
        &:before,
        &:after {
          background-color: var(--BG-button-primary-hover);
        }
      }

      &:before,
      &:after,
      & span {
        position: absolute;
        height: 2px;
        width: 19px;
        top: 0;
        left: 0;
        content: '';
        border-radius: var(--border-radius);
        background-color: var(--color-white);
        transition: var(--transition-default);
      }

      &:after {
        top: 5px;
      }

      & span {
        top: 10px;
      }
    }

    .icons-bar {
      padding: var(--element-padding-s);
      padding-top: var(--element-padding-l);

      &__image {
        width: 40px;
        height: 40px;
        border-radius: var(--border-radius);
        margin-bottom: var(--element-margin-l);
        position: relative;

        &--new-account {
          background-color: var(--BG-button-aside);
          display: flex;
          justify-content: center;
          align-items: center;
          &:focus {
            outline: none;
          }
        }

        &.active {
          ::v-deep img {
            border: var(--border-width-thin) solid var(--color-white);
          }
        }

        ::v-deep img {
          height: 100%;
          width: auto;
        }

        ::v-deep .badge {
          position: absolute;
          top: -10px;
          right: 0;
        }
      }
    }
  }

  &__toggler {
    padding-bottom: var(--element-padding-xxl);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    height: 100%;
    left: -140px;
    width: var(--width-small);
    padding-top: 60px;
    padding-left: var(--element-padding-s);
    background-color: var(--BG-aside-togglable);
    transition: var(--transition-default);
    box-shadow: var(--box-shadow);
  }

  &__btns {
    padding-left: var(--element-padding-s);
    font-size: var(--size-regular);
  }

  &__theme-toggler {
    margin-bottom: var(--element-margin-m);
    margin-top: var(--element-margin-m);
    color: var(--color-white);

    .form-toggler {
      vertical-align: middle;
      margin-left: var(--element-margin-s);
    }
  }

  &__logout-btn {
    background: none;
    color: var(--color-white);
    margin-bottom: var(--element-margin-m);
    margin-top: var(--element-margin-m);
    &:focus {
      outline: none;
      color: var(--color-main-500);
      .logout-icon {
        fill: var(--color-main-500);
      }
    }

    .logout-icon {
      margin-left: var(--element-margin-xs);
      vertical-align: bottom;
      height: 12px;
      fill: var(--color-white);
    }
  }

  h3 {
    padding: var(--element-padding-s);
    background-color: rgba(255, 255, 255, 0.25);
    border-top-left-radius: var(--border-radius);
    border-bottom-left-radius: var(--border-radius);
    margin-bottom: var(--element-margin-xl);
    color: var(--color-white);
    font-size: var(--size-regular);
    font-family: 'OpenSans-Bold', sans-serif;
    line-height: 21px;
  }

  &-navigation {
    padding-top: var(--element-padding-s);
    list-style: none;

    &__link {
      margin-bottom: var(--element-padding-s);
      display: flex;
      align-items: center;

      ::v-deep .badge {
        padding: 0 calc(var(--button-padding-m) / 1.5);
        margin-left: 15px;
        display: block;
      }

      ::v-deep a {
        padding: var(--element-padding-s);
        border-top-left-radius: var(--border-radius);
        border-bottom-left-radius: var(--border-radius);
        text-decoration: none;
        color: var(--color-white);
        font-size: var(--size-regular);
        line-height: 21px;
        width: 100%;
        display: flex;
        transition: var(--transition-default);

        &.router-link-exact-active {
          background-color: var(--BG-navbar);
          border-top-left-radius: var(--border-radius);
          border-bottom-left-radius: var(--border-radius);
        }

        &:hover {
          background-color: var(--BG-navbar);
        }

        &:focus {
          background-color: var(--BG-navbar);
        }
      }
    }
  }
}
</style>
