<template>
  <!-- @TODO need to revise this component for using props from outside -->
  <ul class="action-buttons-group">
    <li class="action-buttons-group__item">
      <button
        @click="$emit('likeClicked')"
        type="button"
        class="action-buttons-group__button"
        :class="{ active: userLiked }"
      >
        <!--@TODO get like BE and send request on click-->
        <like-active-icon v-if="userLiked" />
        <like-normal-icon v-else />
      </button>
      <span class="action-buttons-group__value">{{
        engagementStats.likes
      }}</span>
    </li>
    <li class="action-buttons-group__item">
      <button
        type="button"
        @click.stop="toggleCommentForm"
        class="action-buttons-group__button"
        :class="{ active: showNewCommentForm }"
      >
        <!-- @TODO update svg output to be single image -->
        <comment-normal-icon v-if="!showNewCommentForm" />
        <comment-active-icon v-else />
      </button>
      <span class="action-buttons-group__value">{{
        engagementStats.comments
      }}</span>
    </li>
    <li class="action-buttons-group__item">
      <button type="button" class="action-buttons-group__button">
        <reblog-normal-icon />
      </button>
      <span class="action-buttons-group__value">{{
        engagementStats.reblogs
      }}</span>
    </li>
    <li class="action-buttons-group__item" v-if="userHasEditPermissions">
      <button
        type="button"
        @click.stop="toggleDropdown"
        class="action-buttons-group__button"
      >
        <dots-icon />
      </button>
      <action-dropdown v-if="dropdownShow" @close="handleActionDropdownClose" />
    </li>
    <li v-if="commentsLength" class="action-buttons-group__item">
      <button
        type="button"
        @click="toggleChildComments"
        class="action-buttons-group__button"
      >
        <span
          class="action-buttons-group__text-in-button"
          v-if="showChildComments"
          >Hide comments ({{ commentsLength }})</span
        >
        <span class="action-buttons-group__text-in-button" v-else
          >Show comments ({{ commentsLength }})</span
        >
      </button>
    </li>
  </ul>
</template>
<script>
import LikeNormalIcon from '~svg/like-normal-icon.svg';
import LikeActiveIcon from '~svg/like-active-icon.svg';
import CommentNormalIcon from '~svg/comment-normal-icon.svg';
import CommentActiveIcon from '~svg/comment-active-icon.svg';
import ReblogNormalIcon from '~svg/reblog-normal-icon.svg';
import DotsIcon from '~svg/dots-icon.svg';
import ActionDropdown from '~common/ActionDropdown';

export default {
  components: {
    LikeNormalIcon,
    LikeActiveIcon,
    CommentNormalIcon,
    CommentActiveIcon,
    ReblogNormalIcon,
    DotsIcon,
    ActionDropdown
  },
  props: {
    engagementStats: {
      type: Object,
      required: true
    },
    commentsLength: {
      type: Number
    },
    showNewCommentForm: {
      type: Boolean
    },
    userLiked: {
      type: Boolean
    },
    showChildComments: {
      type: Boolean
    },
    userHasEditPermissions: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      dropdownShow: false
    };
  },
  methods: {
    toggleDropdown() {
      this.dropdownShow = !this.dropdownShow;
    },
    /*@TODO revise click outside*/
    handleActionDropdownClose() {
      this.dropdownShow = false;
    },
    toggleCommentForm() {
      this.$emit('toggleCommentForm');
    },
    toggleChildComments() {
      this.$emit('toggleChildComments');
    }
  }
};
</script>
<style lang="scss" scoped>
.action-buttons-group {
  display: flex;
  margin: 0;
  margin-bottom: var(--element-margin-l);
  padding: 0 var(--element-padding-l);
  &.comment {
    padding: 0;
  }
  &__item {
    margin-right: var(--element-margin-l);
    display: flex;
    align-items: center;
    position: relative;
  }
  &__button {
    background: transparent;
    margin-right: var(--element-margin-xs);
    color: var(--text-button-comments-show);
    font-size: var(--size-regular);
    font-family: 'OpenSans-Bold';
    display: flex;
    &:focus {
      outline: none;
      ::v-deep svg {
        fill: var(--color-main-500);
      }
    }

    > ::v-deep svg {
      fill: var(--text-primary);
      width: 20px;
      height: 20px;
    }

    &.active {
      ::v-deep svg {
        fill: var(--BG-button-pagination-active);
      }
    }
  }
  &__button:hover {
    ::v-deep svg {
      fill: var(--text-secondary);
    }
  }

  &__text-in-button {
    text-transform: uppercase;
  }
  &__value {
    color: var(--text-primary);
    font-size: var(--size-small);
    font-family: 'OpenSans-Bold';
  }
}
</style>
