<template>
  <section class="tab-content">
    <section-header
      :btnSecondaryText="btnSecondary"
      :btnPrimaryText="btnPrimary"
      :showEditIcon="editIcon"
      :title="title"
      @handleEditBtn="$emit('handleEditBtn')"
      @handleSecondaryBtn="$emit('handleSecondaryBtn')"
      @handlePrimaryBtn="$emit('handlePrimaryBtn')"
    />

    <form>
      <!-- @TODO make search -->
      <form-input
        v-if="showSearch"
        class="tab-content__search-input"
        type="search"
        name="find-blog"
        placeholder="Find blog"
      />
    </form>
    <div class="tab-content__container">
      <slot />
    </div>
  </section>
</template>

<script>
import SectionHeader from '../common/SectionHeader';
import FormInput from '../common/Fields/FormInput';

export default {
  components: {
    FormInput,
    SectionHeader
  },
  props: {
    btnSecondary: {
      type: String
    },
    btnPrimary: {
      type: String
    },
    title: {
      type: String
    },
    editIcon: {
      type: Boolean
    },
    showSearch: {
      type: Boolean,
      default: false
    }
  }
};
</script>
<style lang="scss" scoped>
.tab-content {
  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: var(--element-margin-m);
  }

  &__search-input {
    margin-bottom: var(--element-padding-l);
  }
}

::v-deep .form-input {
  input {
    border-color: var(--border-input-search);
    background-color: var(--BG-blog-post);
  }
}
</style>
