<template>
  <div class="comments">
    <new-comment-form
      :newComment="newComment"
      @input="comment => $emit('onNewCommentInput', comment)"
      @createComment="$emit('createComment')"
    />
    <div v-if="comments && comments.length" class="comments-list">
      <comment
        @input="comment => $emit('onNewCommentInput', comment)"
        :key="index"
        v-for="(comment, index) in comments"
        :comment="comment"
        @createComment="$emit('createComment', $event)"
      />
    </div>
  </div>
</template>
<script>
import NewCommentForm from '@/components/common/Comments/NewCommentForm';
import Comment from '@/components/common/Comments/Comment';

export default {
  components: {
    NewCommentForm,
    Comment
  },
  props: {
    comments: {
      /** @TODO add default value */
      type: Array
    },
    newComment: {
      type: String
    }
  }
};
</script>
<style lang="scss" scoped>
.comments-list {
  margin: 0 var(--element-margin-l);
  ::v-deep .child {
    margin: 0;
  }
}
</style>
