<template>
  <section class="create-rules">
    <section-header title="Rules" />
    <form class="create-rules-container">
      <div
        class="create-rules__container"
        v-for="(rule, index) in rules"
        :key="index"
      >
        <form-input
          labelText="Rule Name"
          class="create-rules__input"
          type="text"
          :id="rule.nameId"
          name="rule-name"
          v-model="rule.ruleHeading"
        />
        <form-input
          labelText="Description"
          class="create-rules__input"
          type="text"
          :id="rule.descriptionId"
          name="description"
          v-model="rule.ruleText"
        />
      </div>
      <form-button
        :plus="true"
        text="+ Add rule"
        class="create-rules__plus-btn"
        @click.native.stop="newRule"
      />
      <div class="create-rules__buttons-group">
        <form-button
          type="button"
          class="create-rules__cancel-btn"
          text="Cancel"
          @click.native.stop="$emit('handleCancelBtn')"
        />
        <form-button
          type="button"
          class="create-rules__save-btn button--submit"
          text="Save"
          @click.native.stop="$emit('handleSaveBtn')"
        />
      </div>
    </form>
  </section>
</template>

<script>
import FormInput from '../common/Fields/FormInput';
import SectionHeader from '../common/SectionHeader';
import FormButton from '~common/Buttons/FormButton';

export default {
  components: {
    FormInput,
    SectionHeader,
    FormButton
  },
  props: {
    rules: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    newRule() {
      /** @TODO revise to use @here https://vuejs.org/v2/api/#vm-set */
      /** @TODO revise default data */
      this.rules.push({
        ruleHeading: 'Rule Name',
        ruleText: 'Description',
        descriptionId: 11,
        nameId: 12
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.create-rules {
  padding: var(--element-padding-xl);
  padding-top: 0;

  &-container {
    padding: var(--element-padding-xl);
    background-color: var(--BG-blog-post);
    border-radius: var(--border-radius);
  }
  &__input {
    margin-bottom: var(--element-margin-m);
  }
  &__cancel-btn {
    margin-right: var(--element-margin-m);
  }

  &__buttons-group {
    border-top: var(--border-width-thin) solid var(--divider-in-popup);
    padding-top: var(--element-padding-m);

    > ::v-deep button {
      min-width: 90px;
    }
  }
}
::v-deep {
  .button--submit {
    background-color: var(--BG-button-secondary);
    color: var(--text-button-secondary);
    &:focus {
      background-color: var(--BG-button-secondary-hover);
      border-color: var(--border-button-secondary-hover);
      color: var(--text-button-comment);
    }
    &:hover {
      background-color: var(--BG-button-secondary-hover);
      border-color: var(--border-button-secondary-hover);
      color: var(--text-button-comment);
    }
    &.button--disabled,
    &.button--disabled:hover {
      cursor: not-allowed;
      background-color: var(--BG-button-secondary-disabled);
      border-color: var(--border-button-secondary-disabled);
      color: var(--text-button-comment);
    }
  }
}
</style>
