<template>
  <label class="toggler">
    <input
      type="checkbox"
      :id="id"
      :name="name"
      :value="value"
      v-model="model"
      :required="required"
    />
    <span class="toggler__slider"></span>
  </label>
</template>

<script>
import formCheckboxMixin from '@/mixins/form-checkbox.mixin';

export default {
  mixins: [formCheckboxMixin]
};
</script>

<style lang="scss" scoped>
.toggler {
  position: relative;
  display: inline-block;
  width: 37px;
  height: 19px;
  input {
    opacity: 0;
    width: 0;
    height: 0;
    &:checked {
      + .toggler__slider:before {
        transform: translateX(17px);
        background-color: var(--color-main-800);
      }
    }
  }

  &__slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--color-white);
    transition: var(--transition-default);
    border-radius: 34px;

    &:before {
      position: absolute;
      content: '';
      height: 17px;
      width: 17px;
      left: 2px;
      bottom: 1px;
      background-color: var(--BG-button-inactive);
      transition: var(--transition-default);
      border-radius: 50%;
    }
  }
}
</style>
