<template>
  <article class="blog-post" @click.stop="openBlogModal">
    <header class="blog-post-header" slot="header">
      <author-title
        :name="data.Alias.name"
        :image="data.Alias.avatar.src"
        :date="data.updatedAt"
        :alt="data.Alias.avatar.alt"
      />
      <tags-list
        v-if="data && data.Tags"
        :list="data.Tags"
        @click.native.stop
      />
    </header>
    <div class="blog-post-body">
      <img
        class="blog-post-body__image"
        v-if="data && data.image"
        :src="data.image.src"
        :alt="data.image.name"
      />
      <p class="blog-post-body__content" @click.stop v-html="data.content" />
    </div>
    <action-buttons-group
      v-if="showActionButtons"
      :engagementStats="data.engagementStats"
      @click.native.stop
      :userHasEditPermissions="data.userHasEditPermissions"
      @likeClicked="postLikeToggle"
      :userLiked="data.userLiked"
    />
    <comments
      slot="footer"
      v-if="showComments"
      :comments="data.Comments"
      :newComment="newComment"
      @onNewCommentInput="comment => (newComment = comment)"
      @createComment="createComment"
    />
  </article>
</template>

<script>
/*@TODO revise name since src\views\BlogPost.vue has the same*/
import Comments from '@/components/common/Comments/Comments';
import AuthorTitle from '~common/AuthorTitle';
import TagsList from '~common/TagsList';
import ActionButtonsGroup from '~common/ActionButtonsGroup';
import { createComment } from '@/services/comment.service';
import { mapState } from 'vuex';
import { postLike, postUnlike } from '@/services/blog.service';

export default {
  props: {
    data: {
      type: Object,
      required: true
    },
    showComments: {
      type: Boolean,
      default: true
    },
    showActionButtons: {
      type: Boolean,
      default: true
    }
  },
  components: {
    AuthorTitle,
    TagsList,
    ActionButtonsGroup,
    Comments
  },
  data() {
    return {
      newComment: ''
    };
  },
  methods: {
    openBlogModal() {
      this.$emit('openModal', this.data.id);
    },
    async postLikeToggle() {
      const { id, userLiked } = this.data;
      /** @TODO revise */
      const body = [id, { alias: 1 }];
      userLiked ? await postUnlike(...body) : await postLike(...body);
    },
    /*@TODO make it dynamic and revise do we need comment id*/
    async createComment(parentComment) {
      const payload = {
        alias: this.userData.id,
        content: this.newComment
      };
      if (parentComment) {
        payload.parentComment = parentComment;
      } else {
        payload.blogPost = this.data.id;
      }
      try {
        await createComment(payload);
      } catch (e) {
        return false;
      }
    }
  },
  computed: {
    ...mapState({
      userData: state => state.auth.userData
    })
  }
};
</script>
<style lang="scss" scoped>
.feed-news .blog-post {
  &:hover {
    box-shadow: var(--box-shadow);
    cursor: pointer;
  }
}
.blog-post {
  background-color: var(--BG-blog-post);
  border-radius: var(--border-radius);
  transition: var(--transition-default);
  &-header {
    margin: 0 var(--element-margin-l);
    padding-top: var(--element-padding-l);
  }
  &-body {
    &__image {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
    &__content {
      color: var(--text-primary);
      font-size: var(--size-regular);
      line-height: 1.3em;
      margin: var(--element-margin-l);
    }
  }
}
</style>
