import Vue from 'vue';
import VueRouter from 'vue-router';
/*@TODO remove*/
import Testing from '@/views/Testing.vue';
import NewsFeed from '@/views/NewsFeed.vue';
import Blog from '@/views/Blog.vue';
import Community from '@/views/Community.vue';
/** @TODO revise name */
import MyCommunities from '@/views/MyCommunities.vue';
import Followers from '@/views/Followers.vue';
import Following from '../views/Following';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: NewsFeed
  },
  {
    path: '/testing',
    name: 'testing',
    component: Testing
  },
  {
    path: '/newsFeed/:id?',
    name: 'newsFeed',
    props: true,
    component: NewsFeed
  },
  {
    path: '/newsFeed/:id',
    name: 'newsFeed',
    props: true,
    component: NewsFeed
  },
  {
    path: '/blog/:name/:pagination?',
    name: 'blog',
    props: true,
    component: Blog
  },
  {
    path: '/community/:id/:pagination?',
    name: 'community',
    props: true,
    component: Community
  },
  /** @TODO revise name */
  {
    path: '/my-communities',
    name: 'MyCommunities',
    props: true,
    component: MyCommunities
  },
  {
    path: '/followers',
    name: 'Followers',
    props: true,
    component: Followers
  },
  {
    path: '/following',
    name: 'Following',
    props: true,
    component: Following
  }
];

const router = new VueRouter({
  mode: 'abstract',
  base: process.env.BASE_URL,
  routes
});

export default router;
