export default {
  model: {
    prop: 'value',
    event: 'input'
  },
  props: {
    value: {
      type: undefined
    },
    id: {
      type: String
    },
    name: {
      type: String,
      required: true
    },
    required: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleInput(e) {
      this.$emit('input', e.currentTarget.value);
    }
  }
};
