<template>
  <div v-if="data" class="blog">
    <wallpaper v-if="data.coverImage" :src="data.coverImage.src" />
    <add-image v-else buttonText="Add Background" />
    <div class="blog-header">
      <avatar
        class="blog-header__avatar"
        size="xl"
        :file="data.Alias.avatar.src"
        v-if="data.Alias.avatar"
      />
      <add-image class="addAvatar" buttonText="Add Image" v-else />
      <tabs
        :tabs="navTabs"
        @selectTab="$active => setComponent($active, $active)"
        :active="activeTab"
      />
    </div>
    <div class="blog-content">
      <div class="blog-wrapper">
        <sidebar-form v-if="sidebarCreate" />
        <sidebar-info
          :name="data.name"
          :description="data.description"
          :links="data.links"
          v-else
        />
      </div>
      <div class="blog-main">
        <component
          :blogPosts="data.BlogPosts"
          :is="currentComponent"
          @openModal="handleModalOpen"
        />
      </div>
      <div class="blog-wrapper">
        <sidebar-tags
          :tags="data.Tags"
          createText="Tags of your posts will be shown here. You may add them, but others will see them only when you use them in your posts."
        />
      </div>
    </div>
    <!-- /*@TODO add close callback*/ -->
    <create-privacy-group-modal :settings="modalSettings[modalType]" />
    <share-post-modal
      :data="sharePostModalData"
      :settings="sharePostModalSettings"
    />
  </div>
</template>

<script>
import getComponentsByPrefix from '@/utils/getComponentsByPrefix';
import {
  blogRoot,
  blogModalTypes,
  blogAreas,
  getBlog
} from '@/services/blog.service';
import Avatar from '~common/Avatar';
import Tabs from '~common/Tabs';
import tabsMixin from '@/mixins/tabs.mixin';
import SidebarInfo from '~common/SidebarInfo';
import SidebarTags from '~common/SidebarTags';
import SharePostModal from '@/components/Blog/SharePostModal';
import CreatePrivacyGroupModal from '@/components/Blog/CreatePrivacyGroupModal';
// blogAreas
import BlogAllPosts from '@/components/Blog/BlogAllPosts';
import BlogSettings from '@/components/Blog/BlogSettings';
import BlogPrivacyGroup from '@/components/Blog/BlogPrivacyGroup';
import SidebarForm from '~common/SidebarForm';
import Wallpaper from '~common/Wallpaper';
import AddImage from '~common/AddImage';

export default {
  props: {
    name: {
      required: true
    },
    // @TODO revise it
    sidebarCreate: {
      type: Boolean,
      default: false
    }
  },
  components: {
    SharePostModal,
    CreatePrivacyGroupModal,
    Avatar,
    Tabs,
    SidebarInfo,
    SidebarTags,
    // blogAreas (the order should be strict)
    BlogAllPosts,
    BlogSettings,
    BlogPrivacyGroup,
    Wallpaper,
    SidebarForm,
    AddImage
  },
  mixins: [tabsMixin],
  data() {
    return {
      data: null,
      currentPage: 1,
      activeTab: 0,
      currentComponent: null,
      /*@TODO revise*/
      navTabs: [
        {
          title: 'All Posts',
          component: blogAreas.allPosts
        },
        {
          title: 'Settings',
          component: blogAreas.settings
        },
        {
          title: 'Privacy group',
          component: blogAreas.privacyGroup
        }
      ],
      // @TODO revise it
      sharePostModalSettings: {
        show: false,
        title: 'Share post',
        dismissText: 'Cancel',
        actionText: 'Share'
      },
      sharePostModalData: {
        Tags: [
          {
            id: 5202200931223649,
            name: 'batman'
          },
          {
            id: 139573284492231,
            name: 'gotham'
          },
          {
            id: 139573284492232,
            name: 'gotham'
          },
          {
            id: 139573284492233,
            name: 'gotham'
          },
          {
            id: 139573284492234,
            name: 'gotham'
          },
          {
            id: 139573284492235,
            name: 'gotham'
          }
        ],
        Alias: {
          name: 'blog name',
          avatar: {
            src:
              'https://cdn2.iconfinder.com/data/icons/super-hero/154/batman-comics-hero-avatar-head-mask-512.png'
          }
        },
        dateCreated: '01-24-2019, 07:21',
        dateUpdated: '01-26-2019, 12:21',
        content:
          "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum",
        image: {
          src:
            'https://i.pinimg.com/originals/2b/52/7d/2b527dfbe670cd5a746425eb30e2740d.jpg',
          name: 'gotham city'
        }
      },
      blogModalTypes,
      modalType: blogModalTypes.createPrivacyGroup,
      modalSettings: {
        [blogModalTypes.createPrivacyGroup]: {
          show: false,
          title: 'Create new privacy group',
          dismissText: 'Cancel',
          actionText: 'Save'
        }
      }
    };
  },
  computed: {
    blogComponents() {
      return getComponentsByPrefix.call(this, blogRoot);
    }
  },
  methods: {
    handleModalOpen(modalType) {
      this.modalType = modalType;
      this.modalSettings[modalType].show = true;
    },
    setComponent(tabIndex = 0, componentIndex = 0) {
      this.handleSetActiveTab(tabIndex);
      this.currentComponent = this.blogComponents[componentIndex];
    },
    async getData() {
      try {
        this.data = await getBlog(this.name, this.currentPage);
      } catch (e) {
        return false;
      }
    }
  },
  async mounted() {
    await this.getData();
    this.setComponent();
  }
};
</script>
<style lang="scss" scoped>
.blog {
  grid-area: content;
  overflow: auto;
  background-color: var(--BG-primary);

  &-header {
    display: grid;
    align-items: end;
    grid-template-rows: minmax(0, 60px);
    grid-template-columns: var(--width-small) max-content var(--width-small);
    grid-gap: var(--element-margin-xl);
    background-color: var(--BG-blog-post);
    padding: 0 var(--element-padding-xl);
    margin-bottom: var(--element-margin-xl);
    &__avatar {
      border: var(--border-width-avatar) solid var(--color-white);
    }
  }
  &-content {
    padding: 0 var(--element-padding-xl);
    display: grid;
    grid-column-gap: var(--element-margin-xl);
    grid-template-columns: var(--width-small) auto var(--width-small);
  }
}
</style>
