<template>
  <modal
    class="members-modal modal-overlay--centered"
    :settings="settings"
    @save="
      modalType === communityModalTypes.addMembers
        ? inviteMember()
        : deleteMember()
    "
  >
    <template slot="body">
      <p class="members-modal__desc" v-if="!addedUser && !deletedUser">
        The list of people in your security group won’t be visible at your
        community. Only you can see and manage it.
      </p>
      <p v-else-if="addedUser" class="members-modal__user">
        User
        <span class="members-modal__user-name">{{ addedUser }}</span>
        was successfully added to the security group.
      </p>
      <p v-else class="members-modal__user">
        User
        <span class="members-modal__user-name">{{ deletedUser }}</span>
        was deleted from the security group.
      </p>
      <form
        class="members-modal__search-field"
        v-if="modalType === communityModalTypes.deleteMembers"
      >
        <form-input
          class="members-modal__name"
          type="search"
          name="find-community"
          placeholder="Find community"
        />
      </form>
      <div class="members-modal__container">
        <div class="members-modal__wrapper" slot="body">
          <author-title
            v-for="item of data"
            :key="item.id"
            class="group__item"
            :name="item.name"
            :image="item.image"
            :class="{ active: activeItem === item.id }"
            @click.native.stop="activateItem(item.id)"
          />
        </div>
      </div>
    </template>
  </modal>
</template>

<script>
import { deleteItemById } from '@/utils/array';
import { communityModalTypes } from '@/services/community.service';
import Modal from '../common/Modal';
import FormInput from '../common/Fields/FormInput';
import AuthorTitle from '../common/AuthorTitle';
import {
  inviteMember,
  removeFromCommunity
} from '@/services/community.service';

export default {
  components: {
    AuthorTitle,
    Modal,
    FormInput
  },
  props: {
    settings: {
      type: Object,
      required: true
    },
    modalType: {
      type: String,
      default: communityModalTypes[0],
      validator: value => Object.values(communityModalTypes).includes(value)
    },
    communityId: {
      type: Number,
      required: true
    }
  },
  methods: {
    activateItem(id) {
      this.activeItem = id;
    },
    // /*@TODO change alias after store will be done*/
    async inviteMember() {
      const payload = {
        alias: 1,
        invitedUserId: this.activeItem
      };
      try {
        await inviteMember(this.communityId, payload);
        this.updateModalData(true);
      } catch (e) {
        return false;
      }
    },
    async deleteMember() {
      const payload = {
        alias: 1,
        removeMemberId: this.activeItem
      };
      try {
        await removeFromCommunity(this.communityId, payload);
        this.updateModalData(false);
      } catch (e) {
        return false;
      }
    },
    updateModalData(isAdd) {
      const timeOut = 30000;
      deleteItemById(this.activeItem, this.data, indexToDelete => {
        const getUserName = this.data[indexToDelete].name;
        this.activeItem = null;
        if (isAdd) {
          this.addedUser = getUserName;
          setTimeout(() => (this.addedUser = null), timeOut);
        } else {
          this.deletedUser = getUserName;
          setTimeout(() => (this.deletedUser = null), timeOut);
        }
      });
    }
  },
  data() {
    return {
      communityModalTypes,
      activeItem: null,
      addedUser: null,
      deletedUser: null,
      // @TODO remove after connecting api*/
      data: [
        {
          id: '1',
          name: 'member1',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '2',
          name: 'member2',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '3',
          name: 'member3',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '4',
          name: 'member4',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '5',
          name: 'member5',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '6',
          name: 'member6',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '7',
          name: 'member7',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '8',
          name: 'member8',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '9',
          name: 'member9',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '10',
          name: 'member10',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '11',
          name: 'member12',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '12',
          name: 'member12',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '13',
          name: 'member13',
          image: require('@/assets/img/png/Image_29.png')
        },
        {
          id: '14',
          name: 'member14',
          image: require('@/assets/img/png/Image_29.png')
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
.members-modal {
  padding: var(--element-padding-xl);
  &__desc,
  &__user {
    font-family: OpenSans-Regular;
    font-size: var(--size-regular);
    color: var(--text-primary);
    margin-bottom: var(--element-margin-l);
    line-height: 1.5em;
  }
  &__user {
    color: var(--text-secondary);
    &-name {
      font-family: 'OpenSans-Bold', sans-serif;
    }
  }
  &__container {
    max-height: 50vh;
    overflow: auto;
  }
  &__wrapper {
    display: grid;
    grid-gap: var(--element-margin-s);
    grid-template-columns: repeat(2, 1fr);
    padding-right: var(--element-padding-s);
    padding-bottom: var(--element-padding-s);
  }

  &__search-field {
    position: relative;
    align-items: center;
    margin-bottom: var(--element-margin-l);
    background-color: var(--BG-input);
  }
  ::v-deep {
    .modal-body {
      padding-bottom: 0;
    }
    .group__item {
      padding: var(--element-padding-m);
      margin-bottom: 0;
      border: var(--border-width-thin) solid var(--border-group-normal);
      border-radius: var(--border-radius);
      cursor: pointer;
      background-color: var(--BG-input);
      &:hover {
        border-color: var(--border-input-hover);
      }
      &.active {
        border-color: var(--border-active);
      }
    }
  }
}
</style>
