<template>
  <div v-if="data" class="community">
    <!-- @TODO revise it -->
    <wallpaper v-if="data.coverImage" :src="data.coverImage.src" />
    <add-image v-else buttonText="Add Background" />
    <div class="community-header">
      <div class="community-wrapper"></div>
      <tabs
        :tabs="navTabs"
        @selectTab="$active => setComponent($active, $active)"
        :active="activeTab"
      />
    </div>
    <div class="community-content">
      <div class="community-wrapper">
        <!-- @TODO revise it -->
        <sidebar-form v-if="sidebarCreate" />
        <sidebar-info
          :name="data.name"
          :description="data.description"
          :links="data.links"
          :isSubscribe="data.aliasIsMember"
          v-else
          @subscribe="subscribeToCommunity"
          @unsubscribe="unsubscribeFromCommunity"
        />
      </div>
      <div class="community-main">
        <component
          :is="currentComponent"
          :data="data"
          @openModal="handleModalOpen"
          :communityRules="data.rules"
          :communityId="id"
        />
      </div>
      <div class="community-wrapper">
        <!-- @TODO make it dynamically -->
        <sidebar-tags
          :tags="data.tags"
          createText="Tags of your posts will be shown here. You may add them, but others will see them only when you use them in your posts."
        />
      </div>
    </div>
    <moderation-request-modal
      :settings="moderationModalSettings"
      :data="moderationModalData"
    />
    <members-modal
      v-if="modalSettings[modalType].show"
      :modalType="modalType"
      :settings="modalSettings[modalType]"
      :communityId="data.id"
    />
  </div>
</template>

<script>
import getComponentsByPrefix from '@/utils/getComponentsByPrefix';
import {
  communityModalTypes,
  communityAreas,
  communityRoot,
  getCommunity,
  joinCommunity,
  removeFromCommunity
} from '@/services/community.service';
import MembersModal from '@/components/Community/MembersModal';
import Avatar from '~common/Avatar';
import Tabs from '~common/Tabs';
import tabsMixin from '@/mixins/tabs.mixin';
import SidebarForm from '~common/SidebarForm';
import SidebarInfo from '~common/SidebarInfo';
import SidebarTags from '~common/SidebarTags';
import Wallpaper from '~common/Wallpaper';
import AddImage from '~common/AddImage';
import BlogSettings from '@/components/Blog/BlogSettings';
import ModerationRequestModal from '@/components/Community/ModerationRequestModal';
import CommunityMembers from '@/components/Community/CommunityMembers';
import CommunityModerators from '@/components/Community/CommunityModerators';
import CommunityRules from '@/components/Community/CommunityRules';
import CommunityRequests from '@/components/Community/CommunityRequests';
import CommunityCreateRules from '@/components/Community/CommunityCreateRules';

export default {
  props: {
    id: {
      type: String,
      required: true
    },
    // @TODO revise it
    sidebarCreate: {
      type: Boolean,
      default: false
    }
  },
  components: {
    ModerationRequestModal,
    Avatar,
    Tabs,
    SidebarForm,
    SidebarTags,
    AddImage,
    BlogSettings,
    SidebarInfo,
    Wallpaper,
    MembersModal,
    CommunityCreateRules,
    // communityAreas (the order should be strict)
    CommunityMembers,
    CommunityRules,
    CommunityModerators,
    CommunityRequests
  },
  mixins: [tabsMixin],
  data() {
    return {
      data: null,
      currentPage: 1,
      activeTab: 0,
      currentComponent: null,
      // @TODO talk with Dorian about type of data.rules
      navTabs: [
        {
          title: 'Members',
          component: communityAreas.members
        },
        {
          title: 'Rules',
          component: communityAreas.rules
        },
        {
          title: 'Moderators',
          component: communityAreas.moderators
        },
        {
          title: 'Requests',
          component: communityAreas.requests
        }
      ],
      moderationModalSettings: {
        show: false,
        title: 'Moderation request',
        dismissText: 'Decline',
        actionText: 'Accept'
      },
      communityModalTypes,
      modalType: communityModalTypes.addMembers,
      modalSettings: {
        [communityModalTypes.addMembers]: {
          show: false,
          title: 'Choose new members of the security group',
          dismissText: 'Cancel',
          actionText: '+ Add'
        },
        [communityModalTypes.deleteMembers]: {
          show: false,
          title: 'Choose members to delete from the privacy group',
          dismissText: 'Cancel',
          actionText: 'Delete'
        }
      },
      //@TODO change to real data
      moderationModalData: {
        alias: {
          name: 'community name',
          image:
            'https://cdn2.iconfinder.com/data/icons/super-hero/154/batman-comics-hero-avatar-head-mask-512.png'
        },
        content:
          "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum",
        image: {
          src:
            'https://i.pinimg.com/originals/2b/52/7d/2b527dfbe670cd5a746425eb30e2740d.jpg',
          name: 'gotham city'
        },
        tags: [
          {
            id: 5202200931223649,
            name: 'batman'
          },
          {
            id: 139573284492231,
            name: 'gotham'
          },
          {
            id: 139573284492232,
            name: 'gotham'
          },
          {
            id: 139573284492233,
            name: 'gotham'
          },
          {
            id: 139573284492234,
            name: 'gotham'
          },
          {
            id: 139573284492235,
            name: 'gotham'
          }
        ]
      }
    };
  },
  computed: {
    communityComponents() {
      return getComponentsByPrefix.call(this, communityRoot);
    }
  },
  methods: {
    async getData() {
      try {
        this.data = await getCommunity(this.id, this.currentPage);
      } catch (e) {
        return false;
      }
    },
    // @TODO change payload */
    async subscribeToCommunity() {
      const payload = {
        alias: 1
      };
      try {
        await joinCommunity(this.data.id, payload);
        this.data.aliasIsMember = true;
      } catch (e) {
        return false;
      }
    },
    // @TODO implement in future*/
    async unsubscribeFromCommunity() {
      const payload = {
        alias: 1,
        removeMemberId: 1
      };
      try {
        await removeFromCommunity(this.data.id, payload);
        this.data.aliasIsMember = false;
      } catch (e) {
        return false;
      }
    },
    handleModalOpen(modalType) {
      this.modalType = modalType;
      this.modalSettings[modalType].show = true;
    },
    setComponent(tabIndex = 0, componentIndex = 0) {
      this.handleSetActiveTab(tabIndex);
      this.currentComponent = this.communityComponents[componentIndex];
    }
  },
  async mounted() {
    await this.getData();
    this.setComponent();
  }
};
</script>

<style lang="scss" scoped>
.community {
  grid-area: content;
  overflow: auto;
  background-color: var(--BG-primary);

  ::v-deep &-tabs .nav-tabs li button {
    font-size: var(--size-button);
    text-transform: uppercase;
  }
  &-header {
    display: grid;
    align-items: end;
    grid-template-rows: minmax(0, 60px);
    grid-template-columns: var(--width-small) max-content var(--width-small);
    grid-gap: var(--element-margin-xl);
    background-color: var(--BG-blog-post);
    padding: 0 var(--element-padding-xl);
    margin-bottom: var(--element-margin-xl);
  }
  &-content {
    padding: 0 var(--element-padding-xl);
    display: grid;
    grid-gap: var(--element-padding-xl);
    grid-template-columns: var(--width-small) auto var(--width-small);
  }
}
</style>
