import { setApiHeader } from '~api';
import {
  login,
  logout,
  logoutAll,
  createUser,
  getCurrentUser
} from '@/services/auth.service';
import { hasToken, saveToken, destroyToken } from '@/services/jwt.service';
/*@TODO revise it, possibly get the user data from the getCurrentUser*/
import {
  getUserData,
  setUserData,
  removeUserData
} from '@/services/storage.service';

const state = {
  userData: getUserData(),
  isAuthenticated: hasToken()
};

const actions = {
  async login({ commit }, payload) {
    try {
      const res = await login(payload);
      commit('login', res);
      commit('updateAuth', res && res.userData);
    } catch (e) {
      return false;
    }
  },

  async logout({ commit }) {
    try {
      await logout();
      commit('logout');
      commit('updateAuth');
    } catch (e) {
      return false;
    }
  },

  async logoutAll({ commit }) {
    try {
      await logoutAll();
      commit('logoutAll');
      commit('updateAuth');
    } catch (e) {
      return false;
    }
  },

  async createUser(undefined, payload) {
    try {
      await createUser(payload);
    } catch (e) {
      return false;
    }
  },

  async getCurrentUser() {
    try {
      await getCurrentUser();
    } catch (e) {
      return false;
    }
  }
};

const mutations = {
  updateAuth(state, userData) {
    state.isAuthenticated = hasToken();
    userData ? setUserData(userData) : removeUserData();

    state.userData = getUserData();
    setApiHeader();
  },
  login(state, payload) {
    if (payload) {
      saveToken(payload.token);
    }
  },
  logout() {
    destroyToken();
  },
  logoutAll() {
    /*@TODO revise*/
    destroyToken();
  }
};

export default {
  state,
  actions,
  mutations
};
