<template>
  <ul class="action-dropdown" v-click-outside="close">
    <li @click.stop class="action-dropdown__item">
      <button type="button" class="action-dropdown__button special">
        Delete post
      </button>
    </li>
    <li @click.stop class="action-dropdown__item">
      <button type="button" class="action-dropdown__button">Edite post</button>
    </li>
    <li @click.stop class="action-dropdown__item togglable">
      <button type="button" class="action-dropdown__button">
        Enable comments
      </button>
    </li>
    <li @click.stop class="action-dropdown__item togglable">
      <button type="button" class="action-dropdown__button">Visible for</button>
    </li>
  </ul>
</template>

<script>
import ClickOutside from 'vue-click-outside';

export default {
  directives: {
    ClickOutside
  },
  methods: {
    close() {
      this.$emit('close');
    }
  }
};
