<template>
  <div id="app">
    <component :is="layout">
      <router-view />
    </component>
  </div>
</template>

<script>
import { LAYOUTS } from '@/services/layout.service';

export default {
  computed: {
    layout() {
      const { meta = {} } = this.$route;
      const { DEFAULT } = LAYOUTS;

      return meta.layout || DEFAULT;
    }
  }
};
</script>
