<template>
  <div
    class="select"
    :class="{ active: selectToggled }"
    v-click-outside="() => toggleVisibility(false)"
  >
    <button
      type="button"
      class="select__toggler"
      @click="toggleVisibility"
      tabindex="0"
    >
      {{ title }}
    </button>
    <ul v-if="options.length" class="select-options-container">
      <li
        v-for="(option, index) in options"
        :key="option.id || index"
        class="select__option"
      >
        <form-checkbox
          v-if="hasCheckbox"
          :name="option.text"
          @change="$event => handleCheckboxChange($event, option.text)"
          >{{ option.text }}</form-checkbox
        >
        <template v-else>{{ option.text }}</template>
      </li>
    </ul>
  </div>
</template>

<script>
import ClickOutside from 'vue-click-outside';
import FormCheckbox from '~common/Fields/FormCheckbox';

export default {
  components: {
    FormCheckbox
  },
  props: {
    hasCheckbox: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      required: true
    },
    options: {
      type: Array,
      default: () => [],
      required: true
    }
  },
  directives: {
    ClickOutside
  },
  data() {
    return {
      selectToggled: false
    };
  },
  methods: {
    toggleVisibility(isToggle = true) {
      this.selectToggled = isToggle ? !this.selectToggled : false;
    },
    handleCheckboxChange($event, name) {
      this.$emit('checked', { checked: $event, name });
    }
  }
};
</script>

<style lang="scss" scoped>
.select {
  display: inline-block;
  min-width: 250px;
  position: relative;
  font-size: var(--size-button);

  &.active &__toggler {
    color: var(--dropdown-arrow);

    &:after {
      transform: rotate(45deg) translateY(-50%);
      transform-origin: 0 -50%;
    }
  }

  &.active &-options-container {
    display: block;
  }

  &__toggler {
    border-radius: var(--border-radius);
    border: var(--border-width-thin) solid var(--border-button-select);
    padding: var(--element-padding-xs) var(--element-padding-s);
    color: var(--text-primary);
    position: relative;
    width: 100%;
    text-align: left;
    background-color: transparent;
    margin-bottom: var(--element-margin-s);
    min-height: 30px;
    &:focus {
      outline: none;
      border-color: var(--border-button-secondary-hover);
      color: var(--text-button-hover);
    }

    &:hover {
      border-color: var(--border-button-secondary-hover);
      color: var(--text-button-hover);
    }

    &:after {
      content: '';
      width: 6px;
      height: 6px;
      border-left: var(--border-width-default) solid var(--border-button-select);
      border-top: var(--border-width-default) solid var(--border-button-select);
      position: absolute;
      top: 50%;
      right: 9px;
      transform: rotate(225deg) translateY(50%);
      transform-origin: 0 50%;
    }
  }

  &-options-container {
    list-style: none;
    border: var(--border-width-thin) solid var(--BG-navbar);
    background-color: var(--BG-select-options);
    border-radius: var(--border-radius);
    display: none;
    box-shadow: var(--common-box-shadow);
    z-index: var(--z-index-popup);
    width: 100%;
    padding: var(--button-padding-m) var(--element-padding-s) 0
      var(--element-padding-s);
  }

  &__option {
    margin-bottom: var(--element-margin-m);
    color: var(--text-primary);
    display: flex;
    align-items: center;
    word-break: break-word;
    line-height: 1.5em;
  }
}
::v-deep .checkbox {
  &__text {
    color: var(--text-primary);
    font-size: var(--size-button);
  }
  input[type='checkbox']:checked ~ .checkbox__text {
    color: var(--BG-button-pagination-active);
  }
}
</style>
