<template>
  <img :src="file" :class="size" :alt="alt" />
</template>
<script>
const sizePropTypes = ['sm', 'md', 'xl'];

export default {
  props: {
    file: {
      required: true
    },
    size: {
      type: String,
      default: sizePropTypes[0],
      validator: value => sizePropTypes.includes(value)
    },
    alt: {
      type: String,
      default: 'avatar'
    }
  }
};
</script>
<style lang="scss" scoped>
img {
  object-fit: cover;
  border-radius: var(--border-radius);
  &.sm {
    width: 40px;
    height: 40px;
  }
  &.md {
    width: 50px;
    height: 50px;
  }
  &.xl {
    width: var(--width-small);
    height: 200px;
  }
}
</style>
