<template>
  <div class="rich-text-editor">
    <!-- @TODO add proper condition on v-if -->
    <div v-if="false" class="rich-text-editor__image">
      <!-- @TODO make it dynamic -->
      <!-- @TODO remove height -->
      <img
        height="4"
        src="https://i.pinimg.com/originals/2b/52/7d/2b527dfbe670cd5a746425eb30e2740d.jpg"
        alt="downloaded-image"
      />
      <button type="button" class="rich-text-editor__delete-btn">
        Delete image
      </button>
    </div>
    <ul class="rich-text-editor__tools active">
      <li class="rich-text-editor__item" v-for="c in commands" :key="c.id">
        <button type="button" @click="() => handleExecCommand(c.type)">
          <component :is="c.icon" />
        </button>
      </li>
    </ul>
    <div class="textarea-container">
      <div
        ref="editorArea"
        contenteditable
        class="textarea"
        data-placeholder="Type your comment here"
        @input="handleInput"
      >
        {{ value }}
      </div>
      <slot />
    </div>
    <ul class="rich-text-editor__social-links active">
      <li v-for="a in mediaActions" :key="a.id">
        <template v-if="a.type === actions.uploadImage">
          <input
            type="file"
            accept="image/*"
            id="file"
            v-show="false"
            @change="
              $event => handleExecCommand(a.type, $event.target.files[0])
            "
          />
          <label for="file"><component :is="a.icon"/></label>
        </template>
        <button type="button" v-else @click="() => handleExecCommand(a.type)">
          <component :is="a.icon" />
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
import {
  commands,
  mediaActions,
  getQueryCommandState,
  execCommand,
  actions
} from '@/services/rich-text-editor.service';
// media
import LinkIcon from '~svg/media/link.svg';
/** @TODO revise */
// import AudioIcon from '~svg/media/audio.svg';
// import VideoIcon from '~svg/media/video.svg';
import ImageIcon from '~svg/media/image.svg';
// commands
import Bold from '~svg/text-editing/bold.svg';
import InsertUnorderedList from '~svg/text-editing/insertUnorderedList.svg';
import FormatBlockH1 from '~svg/text-editing/formatBlockH1.svg';
import FormatBlockH2 from '~svg/text-editing/formatBlockH2.svg';
import Italic from '~svg/text-editing/italic.svg';
// import Lowercase from '~svg/text-editing/lowercase.svg';
import InsertOrderedList from '~svg/text-editing/insertOrderedList.svg';
import SentenceCase from '~svg/text-editing/sentenceCase.svg';
import StrikeThrough from '~svg/text-editing/strikeThrough.svg';
import Subscript from '~svg/text-editing/subscript.svg';
import Superscript from '~svg/text-editing/superscript.svg';
import Underline from '~svg/text-editing/underline.svg';
// import Uppercase from '~svg/text-editing/uppercase.svg';

export default {
  /** @TODO revise */
  components: {
    LinkIcon,
    // AudioIcon,
    ImageIcon,
    // VideoIcon,
    Bold,
    InsertUnorderedList,
    FormatBlockH1,
    FormatBlockH2,
    Italic,
    // Lowercase,
    InsertOrderedList,
    SentenceCase,
    StrikeThrough,
    Subscript,
    Superscript,
    Underline
    // Uppercase
  },
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      commands,
      mediaActions,
      actions
    };
  },
  methods: {
    handleInput(e) {
      this.$emit('input', e.target.innerHTML);
    },
    handleExecCommand(type, file) {
      const params =
        type === 'uploadImage'
          ? [type, false, '', this.$refs.editorArea, file]
          : [type];
      execCommand(...params);
    },
    getQueryCommandState
  },
  mounted() {
    /** @TODO revise value/v-model functionality */
    this.$refs.editorArea.innerHTML = this.value;
  }
};
</script>

<style lang="scss" scoped>
.textarea {
  height: auto;
  flex-grow: 1;
  min-height: 50px;
  max-height: 350px;
  line-height: normal;
  border: 1px solid var(--border-input-comment-normal);
  font-size: var(--size-regular);
  padding: var(--element-padding-m) var(--element-padding-s);
  border-radius: var(--border-radius);
  background-color: var(--BG-blog-post);
  margin-bottom: var(--element-margin-l);
  color: var(--text-primary);
  margin-right: var(--element-margin-s);
  word-break: break-all;
  overflow: auto;
  resize: vertical;
  ::v-deep a {
    color: var(--text-primary);
  }

  ::v-deep ul {
    padding-left: 40px;
  }

  &:focus {
    outline: none;
  }

  &-container {
    display: flex;
  }

  &:empty {
    &:before {
      content: attr(data-placeholder);
    }
  }

  &:focus:before {
    display: none;
  }
}

.rich-text-editor {
  display: flex;
  flex-direction: column;
  flex: 1 0 0;
  &__delete-btn {
    background-color: var(--color-main-800);
    color: var(--color-white);
    padding: var(--element-padding-s) var(--element-padding-l);
    font-size: var(--size-regular);
    position: absolute;
    bottom: 0;
    right: 0;
    border-radius: var(--border-radius);
    cursor: pointer;
    &:focus {
      outline: none;
    }
  }

  &__image {
    margin-bottom: var(--element-margin-m);
    position: relative;

    img {
      display: block;
      opacity: var(--opacity-image-overlay);
    }
  }

  ::v-deep &__tools {
    margin: 0;
    display: none;
    margin-bottom: var(--element-margin-s);
    list-style: none;
    justify-content: space-between;
    max-width: 390px;
    flex-wrap: wrap;
    &.active {
      display: flex;
    }
    button {
      background-color: transparent;
      color: var(--border-button-tools);
      vertical-align: middle;
      &:focus {
        outline: none;
        svg {
          fill: var(--color-main-600);
        }
      }

      svg {
        fill: var(--text-activity-name-active);
        vertical-align: middle;
      }
    }
  }
  ::v-deep &__item {
    height: 24px;
    flex-grow: 1;
    border: var(--border-width-thin) solid var(--border-button-tools);
    border-left: none;
    text-align: center;
    transition: var(--default-transition);

    &:first-child {
      border-left: var(--border-width-thin) solid var(--border-button-tools);
      border-top-left-radius: var(--border-radius);
      border-bottom-left-radius: var(--border-radius);
    }

    &:last-child {
      border-top-right-radius: var(--border-radius);
      border-bottom-right-radius: var(--border-radius);
    }

    &.active {
      background-color: green;
    }
  }

  &__social-links {
    display: none;
    list-style: none;

    &.active {
      display: flex;
    }
    button,
    label {
      padding: 0 var(--button-padding-s);
      background-color: transparent;
      &:focus {
        outline: none;
        svg {
          fill: var(--color-main-500);
        }
      }

      svg {
        fill: var(--text-primary);
      }
    }
  }
}
</style>
