<template>
  <!-- /*@TODO test this approach through all the app*/ -->
  <div class="form-input" :class="{ transparent: notBordered }">
    <search-icon class="search-icon" v-if="type === 'search'" />
    <label :for="id" v-if="labelText">{{ labelText }}</label>
    <component
      :name="name"
      :id="id"
      :value="value"
      :placeholder="placeholder"
      @input="handleInput"
      :required="required"
      :type="type"
      :is="getElementByType"
    />
  </div>
</template>

<script>
import formInputMixin from '@/mixins/form-input.mixin';
import SearchIcon from '~svg/search-icon.svg';

export default {
  mixins: [formInputMixin],
  props: {
    type: {
      type: String,
      default: 'text'
    },
    placeholder: {
      type: String
    },
    notBordered: {
      type: Boolean,
      default: false
    },
    labelText: {
      type: String
    },
    value: {
      type: String
    }
  },
  computed: {
    /*@TODO add predefined input types*/
    /*@TODO revise textarea case functionality*/
    getElementByType() {
      return this.type === 'textarea' ? 'textarea' : 'input';
    }
  },
  components: {
    SearchIcon
  }
};
</script>

<style lang="scss" scoped>
/*@TODO revise textarea styles case*/
.form-input {
  position: relative;

  &.textarea {
    &.big > textarea {
      font-size: var(--size-primary);
      font-family: 'OpenSans-Bold', sans-serif;
    }

    &.small > textarea {
      height: 41px;
    }
  }
}

input {
  width: 100%;
  border-radius: var(--border-radius);
  border: var(--border-width-thin) solid;
  border-color: var(--border-input-normal);
  padding: var(--element-padding-s);
  font-family: 'OpenSans-Regular';
  font-size: var(--size-regular);
  background-color: transparent;
  color: var(--text-primary);
  transition: var(--transition-default);

  &::placeholder {
    color: var(--color-main-500);
  }

  &:hover {
    border-color: var(--border-input-hover);
  }
  &:focus {
    border-color: var(--border-input-active);
    outline: none;
  }
}
input[type='search'] {
  padding-left: var(--element-padding-xl);
}

textarea {
  width: 100%;
  border-radius: var(--border-radius);
  border: var(--border-width-thin) solid;
  border-color: var(--border-input-normal);
  padding: var(--element-padding-s);
  font-family: 'OpenSans-Regular';
  font-size: var(--size-regular);
  background-color: transparent;
  color: var(--text-primary);
  resize: vertical;
  min-height: 41px;

  &::placeholder {
    color: var(--color-main-500);
  }

  &:hover {
    border-color: var(--border-input-hover);
  }

  &:focus {
    border-color: var(--border-input-active);
    outline: none;
  }
}

.search-icon {
  position: absolute;
  top: 50%;
  left: var(--element-margin-s);
  transform: translateY(-50%);
}

/** @TODO revise styles for the notBordered prop */
.form-input.transparent {
  padding: 0;
  margin-right: var(--element-margin-m);
  input[type='search'] {
    border: none;
    font-size: var(--size-button);
    padding: var(--element-padding-xs);
    padding-left: var(--element-padding-xl);
  }

  input {
    border: none;
  }

  textarea {
    border: none;
  }
}

label {
  font-size: var(--size-button);
  font-family: 'OpenSans-Bold', sans-serif;
  margin-bottom: var(--element-margin-s);
  color: var(--text-primary);
  display: block;
  cursor: default;
}
</style>
