// eslint-disable-next-line no-alert
const createLink = () => prompt('Enter the URL');
/** @TODO add functionality */
const uploadImage = (editorContent, file) => {
  const reader = new FileReader();

  let dataURI;

  reader.addEventListener(
    'load',
    function() {
      dataURI = reader.result;

      const img = document.createElement('img');
      img.src = dataURI;
      editorContent.appendChild(img);
    },
    false
  );

  if (file) {
    reader.readAsDataURL(file);
  }
};
const getFormatBlockArgument = commandName =>
  commandName.substring(actions.formatBlock.length);

export const getQueryCommandState = aCommandName =>
  document.queryCommandState(aCommandName);

export const execCommand = (
  aCommandName,
  aShowDefaultUI = false,
  aValueArgument = '',
  editorContent,
  file
) => {
  /** @TODO create action types */
  switch (aCommandName) {
    case 'createLink':
      aValueArgument = createLink();
      break;
    case actions.uploadImage:
      uploadImage(editorContent, file);
      break;
    case 'formatBlockH1':
    case 'formatBlockH2':
      aValueArgument = getFormatBlockArgument(aCommandName);
      aCommandName = actions.formatBlock;
  }

  document.execCommand(aCommandName, aShowDefaultUI, aValueArgument);
};
