<template>
  <div v-if="requestsData" class="followers-requests">
    <request
      :showButtons="true"
      :name="name"
      activityName="subscribed to your community:"
      :activityText="'@' + data.link"
      :date="dateRequested"
      v-for="{
        alias: { id, image, name },
        dateRequested
      } in requestsData.membershipRequests"
      :key="id"
      :image="image"
      @decline="() => handleUserRequest(id)"
      @accept="() => handleUserRequest(id, true)"
    />
  </div>
</template>

<script>
import {
  communityAreas,
  getCommunityRequests,
  declineRequest,
  acceptRequest
} from '@/services/community.service';
import Request from '@/components/Blog/Request';

export default {
  name: communityAreas.requests,
  components: {
    Request
  },
  props: {
    communityId: {
      required: true,
      type: String
    },
    data: {
      type: Object
    }
  },
  data() {
    return {
      requestsData: null,
      currentPage: 1,
      aliasId: 1, // @TODO get it from the store
      communityRequestData: {
        alias: 1, // @TODO and use it here
        requestId: null
      }
    };
  },
  methods: {
    async getData() {
      try {
        this.requestsData = await getCommunityRequests(
          this.communityId,
          this.currentPage
        );
      } catch (e) {
        return false;
      }
    },
    async handleUserRequest(memberId, isAccept) {
      this.communityRequestData.requestId = memberId;
      const payload = [this.communityId, this.requestData];
      if (isAccept) {
        try {
          await acceptRequest(...payload);
          await this.getData();
        } catch (e) {
          return false;
        } finally {
          this.communityRequestData.requestId = null;
        }
      } else {
        try {
          await declineRequest(...payload);
          await this.getData();
        } catch (e) {
          return false;
        } finally {
          this.communityRequestData.requestId = null;
        }
      }
    }
  },
  async mounted() {
    await this.getData();
  }
};
</script>
<style lang="scss" scoped>
.followers-requests {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: var(--element-margin-m);
}
</style>
