<template>
  <modal class="blog-post" v-if="data" :settings="settings">
    <blog-post slot="body" :data="data" />
  </modal>
</template>
<script>
import Modal from '~common/Modal';
import BlogPost from '@/components/Blog/BlogPost';
import { getBlogPost } from '@/services/blog.service';

export default {
  props: {
    id: {
      required: true
    },
    settings: {
      type: Object,
      required: true
    }
  },
  components: {
    Modal,
    BlogPost
  },
  data() {
    return {
      data: null
    };
  },
  methods: {
    async getData() {
      try {
        this.data = await getBlogPost(this.id);
      } catch (e) {
        return false;
      }
    }
  },
  async mounted() {
    await this.getData();
  }
};
</script>
<style lang="scss" scoped>
::v-deep {
  .blog-post {
    &-header {
      margin: 0 var(--element-margin-l);
    }
    &-body {
      &__image {
        width: 100%;
        height: auto;
        object-fit: cover;
      }
      &__content {
        color: var(--text-primary);
        font-size: var(--size-regular);
        line-height: 1.3em;
        margin: var(--element-margin-l);
      }
    }
  }
  .modal {
    width: var(--width-large);
    overflow: visible;
    position: relative;
    margin: 20px auto;

    &-content {
      margin: 0;
      border-radius: 0;
    }
    &-header {
      margin: 0;
      padding: 0;
      &-btn__close {
        top: 20px;
        right: 20px;
        transform: initial;
        z-index: var(--z-index-1);
      }
    }
    &-body {
      padding: 0;
    }
    &-footer {
      padding: 0;
    }
  }
}
</style>
