<template>
  <form class="settings">
    <section-header title="Settings" />
    <div class="settings-container">
      <div
        v-for="([key, { title, data, id }], index) in Object.entries(settings)"
        :key="`${title}-${index}`"
        class="radio-button-group"
      >
        <h4 class="radio-button-group__title">{{ title }}</h4>
        <template v-for="(dataItem, index) in data">
          <form-radio-button
            :name="key"
            :value="dataItem"
            v-model="formData[key]"
            :key="`${id}-${index}`"
          >
            <template>{{ dataItem.text }}</template>
          </form-radio-button>
          <!-- @TODO output select only if button checked -->
          <form-select
            :key="`${id}-${index}-select`"
            v-if="dataItem.selectOptions"
            title="security group name"
            :options="dataItem.selectOptions"
          />
        </template>
      </div>

      <div class="settings-container__btns">
        <form-button type="reset" text="Cancel" />
        <form-button type="submit" text="Save" />
      </div>
    </div>
  </form>
</template>

<script>
import { blogAreas } from '@/services/blog.service';
import FormRadioButton from '@/components/common/Fields/FormRadioButton';
import FormSelect from '@/components/common/Fields/FormSelect';
import FormButton from '~common/Buttons/FormButton';
import SectionHeader from '../common/SectionHeader';

export default {
  name: blogAreas.settings,
  components: {
    FormSelect,
    FormButton,
    FormRadioButton,
    SectionHeader
  },
  // @TODO  make data dynamic
  data() {
    return {
      formData: {
        subscription: null,
        content: null
      },
      settings: {
        subscription: {
          id: 'subscription',
          title: 'Subscription (Example)',
          data: [
            {
              text: 'Opened (your approval is not required)'
            },
            {
              text: 'Opened (your approval is not required)'
            },
            {
              text: 'Invite Only (your invitation required)'
            }
          ]
        },
        content: {
          id: 'content',
          title: 'Content',
          data: [
            {
              text: 'Everybody can see your posts'
            },
            {
              text: 'Only subscribers can see your posts'
            },
            {
              text: 'Only members of privacy groups can see your posts'
            },
            {
              text: 'Only you can see your posts'
            }
          ]
        },
        comments: {
          id: 'Comments',
          title: 'Comments',
          data: [
            {
              text: 'Everybody can leave comments'
            },
            {
              text: 'Only subscribers can leave comments'
            },
            {
              text: 'Only members of privacy groups can leave comments'
            },
            {
              text: 'Only you can leave comments'
            }
          ]
        },
        likes: {
          id: 'Likes',
          title: 'Likes',
          data: [
            {
              text: 'Everybody can see posts you liked'
            },
            {
              text: 'Only subscribers can see posts you liked'
            },
            {
              text:
                'Only members of privacy groups can see the posts you liked',
              selectOptions: [
                { id: 1, text: 'security group name' },
                { id: 2, text: 'security group name' },
                { id: 3, text: 'security group name' },
                { id: 4, text: 'security group name' },
                { id: 5, text: 'security group name' }
              ]
            },
            {
              text: 'Only you can leave comments'
            }
          ]
        },
        users: {
          id: 'users',
          title: 'Users you follow',
          data: [
            {
              text: 'Everybody can see users you follow'
            },
            {
              text: 'Only subscribers can see users you follow'
            },
            {
              text: 'Only members of privacy groups can see users you follow'
            },
            {
              text: 'Only you can see users you follow'
            }
          ]
        }
      }
    };
  }
};
</script>
<style lang="scss" scoped>
.settings {
  margin-bottom: var(--element-margin-xxl);
  &-container {
    background-color: var(--BG-blog-post);
    border-radius: var(--border-radius);
    padding: var(--element-padding-xl);

    &__btns {
      padding-top: var(--element-padding-l);
      position: relative;

      &:before {
        content: '';
        border-top: var(--border-width-thin) solid var(--divider-in-popup);
        width: 430px;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}

::v-deep {
  .radio-button-group {
    margin-bottom: var(--element-margin-l);
    font-size: var(--size-regular);
    h4 {
      font-family: 'OpenSans-Bold', sans-serif;
      color: var(--text-primary);
      margin-bottom: var(--element-margin-m);
    }
    .radiobutton:last-child {
      margin-bottom: var(--element-margin-l);
    }
  }

  .button {
    height: 30px;
    min-width: 90px;
    &:first-child {
      margin-right: var(--element-margin-s);
    }
  }

  .select {
    margin-left: var(--element-margin-xl);
    font-size: var(--size-regular);
    &__toggler {
      padding: var(--button-padding-s) var(--element-padding-s);
    }
  }
  .checkbox__text {
    font-size: var(--size-regular);
  }
}
</style>
