<template>
  <button
    type="button"
    v-click-outside="() => toggleDropdown(false)"
    class="dots-button"
    @click.stop="toggleDropdown"
  >
    <dots-icon class="dots-button__icon" :class="{ active: dropdownShow }" />
    <action-dropdown v-if="dropdownShow" />
  </button>
</template>
<script>
import DotsIcon from '~svg/dots-icon.svg';
import ActionDropdown from '~common/ActionDropdown';
import ClickOutside from 'vue-click-outside';

export default {
  components: {
    DotsIcon,
    ActionDropdown
  },
  data() {
    return {
      dropdownShow: false
    };
  },
  directives: {
    ClickOutside
  },
  methods: {
    toggleDropdown(isToggle = true) {
      this.dropdownShow = isToggle ? !this.dropdownShow : false;
    }
  }
};
</script>
<style lang="scss" scoped>
.dots-button {
  position: relative;
  outline: none;
  background: transparent;

  &__icon {
    fill: var(--text-primary);
    width: 20px;
    height: 13px;
  }
}
.active {
  fill: var(--BG-button-pagination-active);
}
</style>
