<template>
  <!-- take a conversation with @Vika if we need more editable field via props here -->
  <div class="sidebar-info">
    <h3 class="sidebar-info__title">{{ name }}</h3>
    <form-button
      @click.native.stop="$emit('subscribe')"
      type="submit"
      text="Subscribe"
      v-if="!isSubscribe"
    />
    <form-button
      type="cancel"
      text="Unsubscribe"
      @click.native.stop="$emit('unsubscribe')"
      v-else
    />
    <div class="sidebar-info__desc" v-html="description" />
    <h4 class="sidebar-info__subtitle">My Links</h4>
    <ul class="sidebar-info-list">
      <li
        class="sidebar-info-list__item"
        :key="index"
        v-for="(link, index) in links"
      >
        <a
          :href="link.link"
          target="_blank"
          rel="noopener"
          class="sidebar-info__link"
          >{{ link.text }}</a
        >
      </li>
    </ul>
  </div>
</template>
<script>
import FormButton from '~common/Buttons/FormButton';
export default {
  components: {
    FormButton
  },
  props: {
    name: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    links: {
      type: Array
    },
    isSubscribe: {
      type: Boolean
    }
  }
};
</script>
<style lang="scss" scoped>
.sidebar-info {
  background-color: var(--BG-blog-post);
  display: flex;
  flex-direction: column;
  border-radius: var(--border-radius);
  padding: var(--element-padding-l);
  color: var(--text-primary);
  font-size: var(--size-regular);
  &__title {
    font-family: 'OpenSans-Bold';
    font-size: var(--size-primary);
    text-align: center;
    margin-bottom: var(--element-margin-m);
  }
  &__desc {
    line-height: 1.5em;
    margin: var(--element-margin-m) 0;
  }
  &__subtitle {
    font-family: 'OpenSans-Bold';
    margin-bottom: var(--element-margin-s);
  }
  &-list {
    list-style: none;
    margin: 0;
    &__item {
      margin-bottom: var(--element-margin-s);
    }
  }
  &__link {
    color: var(--text-secondary);
  }
}
</style>
