<template>
  <div class="feed pinterest">
    <div v-if="data" class="feed-news ">
      <blog-post-modal
        :settings="blogModalSettings"
        v-if="modalBlogId"
        :id="modalBlogId"
      />
      <!-- @TODO pass userHasEditPermissions to component -->
      <blog-post
        :key="blog.id"
        :data="blog"
        :showComments="false"
        v-for="blog in data.blogPosts"
        @openModal="openBlogModal"
      />
    </div>
    <div v-if="data" class="feed-popular-blogs">
      <h3 class="feed-popular-blogs__title">popular communities</h3>
      <!-- @TODO switch key to blogInfo.id (currently there's an issue on BE so the ids are duplicated) -->
      <popular-blog
        :key="index"
        v-for="(blogInfo, index) in data.suggestions"
        :blogInfo="blogInfo"
      />
    </div>
    <footer>
      <pagination />
    </footer>
  </div>
</template>

<script>
import { defaultPage, getNewsFeed } from '@/services/feed.service';
import BlogPost from '@/components/Blog/BlogPost';
import BlogPostModal from '@/components/Blog/BlogPostModal';
import Pagination from '../components/common/Pagination';
import PopularBlog from '~common/PopularBlog';

export default {
  props: {
    id: {
      type: Number,
      default: defaultPage
    }
  },
  components: {
    PopularBlog,
    BlogPost,
    BlogPostModal,
    Pagination
  },
  data() {
    return {
      data: null,
      modalBlogId: null,
      blogModalSettings: {
        hideHeader: true,
        show: false
      }
    };
  },
  methods: {
    async getData() {
      try {
        this.data = await getNewsFeed(this.id);
      } catch (e) {
        return false;
      }
    },
    openBlogModal(id) {
      this.modalBlogId = id;
      this.blogModalSettings.show = true;
    }
  },
  async mounted() {
    await this.getData();
  }
};
</script>
<style lang="scss" scoped>
.feed {
  overflow-y: auto;
  grid-area: content;
  padding: var(--element-padding-xl);
  display: grid;
  grid-template-columns: 65% 15%;
  grid-column-gap: var(--element-padding-xl);

  &.pinterest {
    grid-template-columns: 85% 15%;
    padding: var(--element-padding-l);
    grid-column-gap: var(--element-padding-m);
  }

  &-popular-blogs {
    padding-left: var(--element-padding-m);

    &__title {
      color: var(--text-primary);
      text-transform: uppercase;
      line-height: var(--size-button);
      padding-top: var(--element-padding-xs);
      padding-bottom: var(--element-padding-m);
      margin-bottom: var(--element-margin-m);
      font-family: 'OpenSans-Bold', sans-serif;
      font-size: var(--size-button);
      border-bottom: var(--border-width-thin) solid var(--popular-blog-divider);
    }
  }
  &-news {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: var(--element-margin-m);
  }
}
</style>
